<template>
  <footer class="footer-guide">
    <a class="guide-item" :class="{on: '/msite'=== $route.path}" @click="goTo('/msite')">
      <span class="item-icon">
        <i class="iconfont icon-waimai-"></i>
      </span>
      <span>外卖</span>
    </a>
    <a class="guide-item" :class="{on: '/search'=== $route.path}" @click="goTo('/search')">
      <span class="item-icon">
        <i class="iconfont icon-sousuo"></i>
      </span>
      <span>搜索</span>
    </a>
    <a class="guide-item" :class="{on: '/order'=== $route.path}" @click="goTo('/order')">
      <span class="item-icon">
        <i class="iconfont icon-dingdan"></i>
      </span>
      <span>订单</span>
    </a>
    <a class="guide-item" :class="{on: '/profile'=== $route.path}" @click="goTo('/profile')">
      <span class="item-icon">
        <i class="iconfont icon-wode"></i>
      </span>
      <span>我的</span>
    </a>
  </footer>
</template>
<script>
  export default {
    methods: {
      goTo(path) {
        this.$router.replace(path)
      }
    }
  }
</script>

<style lang="stylus" ref="stylesheet/stylus">
  @import "../../common/stylus/mixins.styl"
  .footer-guide
    top-border-1px(#e4e4e4)
    position fixed
    z-index 100
    left 0
    bottom 0
    background-color: #fff;
    width 100%
    height 50px
    display flex
    .guide-item
      display flex
      flex 1
      text-align center
      flex-direction column
      align-items center
      margin 5px
      color #999999
      &.on
        color #02a744
      span
        font-size 12px
        margin-top 2px
        margin-bottom 2px
        .iconfont
          font-size 22px
</style>



